@charset "UTF-8";
.mobile-header {
  display: none;
  text-align: center;
  font-family: "Avenir Heavy", sans-serif;
  line-height: 1.75;
  letter-spacing: 0;
  margin-bottom: 0.5rem;
}

.table {
  box-shadow: 2px -2px 3px 3px #ccc;
  border-radius: 16px;
  margin: 4rem;
  border-spacing: 0;
  margin: 0px;
}
.table thead th:first-child {
  border-top-left-radius: 16px;
}
.table thead th:last-child {
  border-top-right-radius: 16px;
}
.table-primary th {
  background-color: rgba(128, 128, 128, 0.1);
}
.table-secondary th {
  background-color: rgba(96, 150, 108, 0.3);
}
.table th {
  padding: 24px;
  font-family: "Avenir Heavy", sans-serif;
  line-height: 1.75;
  letter-spacing: 0;
  cursor: pointer;
  font-size: 20px;
}
.table td {
  padding: 24px;
  font-family: "Avenir Book", sans-serif;
  line-height: 1.75;
  letter-spacing: 0.1;
  font-size: 20px;
}
.table td .selectionBtn {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  accent-color: #5c50bb;
}
.table tr {
  position: relative;
}
.table tr:not(:last-child):after {
  content: "";
  position: absolute;
  left: 10px;
  right: 10px;
  bottom: 0;
  border-bottom: 1px solid #ddd;
}
.table .sort-asc::after {
  content: " ↑";
}
.table .sort-desc::after {
  content: " ↓";
}
.table-selected-row-primary {
  background-color: #efedfd;
}
.table-selected-row-secondary {
  background-color: #aea2ef;
}
@media (max-width: 780px) {
  .table {
    box-shadow: 2px -2px 3px 3px #ccc;
    border-radius: 8px;
    margin: 4rem;
    border-spacing: 0;
  }
  .table thead th:first-child {
    border-top-left-radius: 8px;
  }
  .table thead th:last-child {
    border-top-right-radius: 8px;
  }
  .table th {
    padding: 8px 16px;
    font-family: "Avenir Heavy", sans-serif;
    line-height: 1.75;
    letter-spacing: 0;
    cursor: pointer;
    font-size: 20px;
  }
  .table td {
    padding: 8px 16px;
    font-size: 20px;
  }
  .table td .selectionBtn {
    width: 24px;
    height: 24px;
    border-radius: 50%;
    accent-color: #5c50bb;
  }
  .table tr {
    position: relative;
  }
}
@media (max-width: 600px) {
  .table {
    display: flex;
    flex-direction: column;
    margin-top: 0px;
  }
  .table tr {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .table tr:not(:last-child):after {
    content: "";
    position: absolute;
    left: 10px;
    right: 10px;
    bottom: 0;
    border-bottom: 1px solid #ddd;
  }
  .table tr td:nth-child(n+3) {
    flex-basis: 100%;
  }
  .table tr td:first-child {
    flex-basis: 5%;
  }
  .table tr td:nth-child(2) {
    flex-basis: 60%;
  }
  .table th,
  .table td {
    flex-basis: 100%;
    border: none;
    font-size: 16px;
  }
  .table th {
    background-color: #f2f2f2;
    font-weight: bold;
  }
  .table .select-column {
    width: auto;
  }
  .table thead {
    display: none;
  }
}

@media (max-width: 600px) {
  .mobile-header {
    display: block;
  }
}